import { Injectable } from '@angular/core';
import {Router} from '@angular/router'
import {AngularFireAuth} from 'angularfire2/auth';
import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import * as firebase from 'firebase/app';
import { Observable} from 'rxjs';
import {User} from '../models/user.model';
import { error } from '@angular/compiler/src/util';
import { stringify } from '@angular/core/src/render3/util';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private user: Observable<firebase.User>;
  private authState: any;



  constructor(private afAuth: AngularFireAuth, private db: AngularFireDatabase, private router:Router) { 
    this.user = afAuth.authState;
  }

  get currentUserId(): string {
    
    return this.authState.user != null ? this.authState.user.uid : '';
  }

  login(email: string, password:string)
  {
      return this.afAuth.auth.signInWithEmailAndPassword(email,password)
      .then((resolve)=>{
        const status = 'online';
        this.setUserStatus(status)
        this.router.navigate(['chat'])
      })
  }

  signUp(email: string,password: string,displayName: string ){
      return this.afAuth.auth.createUserWithEmailAndPassword(email,password)
      
      .then((user)=>{
        console.log("USER :" + user.user.uid)
        this.authState = user
        const status = 'online'
        this.setUserData(email,displayName, status,`otherUser/${this.currentUserId}`);
      }).catch(error=>console.log(error));
  }

  setUserData(email: string, displayName: string, status: string, path: string){
    //const path = `users/${this.currentUserId}`;
    const data = {
      email: email,
      displayName: displayName,
      status: status

    };
    this.db.object(path).update(data)
    .catch(error=>console.log(error))

  }
  setUserStatus( status: string){
    const path = `users/${this.currentUserId}`;
    const data = {
      status: status

    };

    this.db.object(path).update(data)
    .catch(error => console.log(error));

    
  }

  authUser(){
    return this.user;
  }

  logout() {
    this.afAuth.auth.signOut();
    this.router.navigate(['login']);
  }

  doGoogleLogin(){
    return new Promise<any>(async (resolve, reject) => {
      const provider = new firebase.auth.GoogleAuthProvider();
      provider.addScope('profile');
      provider.addScope('email');
      const credential = await this.afAuth.auth.signInWithPopup(provider);
      this.setUserData(credential.user.email,credential.user.displayName,'online',`users/${credential.user.uid}`)
      console.log("USERNAME : ",credential.user.displayName);
      this.router.navigate(['chat']);
      /*const credential =this.afAuth.auth
      .signInWithPopup(provider)
      .then(res => {
        resolve(res);
      })*/ 
      
    })
  }

  //https://chat-room-c61ac.firebaseapp.com/__/auth/handler
  doFacebookLogin(){
    return new Promise<any>(async (resolve, reject) => {
      let provider = new firebase.auth.FacebookAuthProvider();
      provider.addScope('profile');
      provider.addScope('email');
      const credential = await this.afAuth.auth.signInWithPopup(provider);
      this.setUserData(credential.user.email,credential.user.displayName,'online',`users/${credential.user.uid}`)
      console.log("USERNAME : ",credential.user.displayName);
      this.router.navigate(['chat']);
      
      
    })
    //this.setUserData(credential.user.email,credential.user.displayName,'online',`users/${credential.user.uid}`)
 }

 updateOnDisconnect(){
  
   firebase.database().ref().child('users/' + this.authState.user.uid).onDisconnect().update({
    status: "offline"

  })
 }

 updateOnConnect(){
   return this.db.object('.info/connected')
 }

 

  
}
