import { Component, OnInit, OnChanges } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';

import { ChatService } from '../services/chat.service';
import { Observable } from 'rxjs';
import { ChatMessage } from '../models/chat-message.model';
import { AngularFireDatabase,AngularFireList } from 'angularfire2/database';
@Component({
  selector: 'app-feed',
  templateUrl: './feed.component.html',
  styleUrls: ['./feed.component.css']
})
export class FeedComponent implements OnInit, OnChanges {
  feed: Observable<any>;
  user: any;
  addMsg: number;
  constructor(private chat: ChatService,private db: AngularFireDatabase,
    private afAuth: AngularFireAuth) { this.afAuth.authState.subscribe(auth =>{
      if(auth !== undefined && auth != null){
        this.user = auth
      }
    })}

  ngOnInit() {
    console.log("feed initializing")
    this.addMsg = 0;
    this.feed = this.db.list('/Chat', ref => ref.orderByKey().limitToLast(20+this.addMsg)).valueChanges()/// <reference path="" />
    
    console.log(this.feed)
  }

  ngOnChanges() {
    this.feed = this.db.list('/Chat', ref => ref.orderByKey().limitToLast(20+this.addMsg)).valueChanges();
  }

  addMessages(){
    this.addMsg+=10;
    this.feed = this.db.list('/Chat', ref => ref.orderByKey().limitToLast(20+this.addMsg)).valueChanges();
    console.log(15+this.addMsg);

  }

}
