import { Component } from '@angular/core';
import {Router} from '@angular/router';
import { ChatService } from './services/chat.service';
import { AngularFireDatabase } from 'angularfire2/database';
import{AngularFireStorage} from '@angular/fire/storage';
import { AngularFireAuth } from 'angularfire2/auth';
import { finalize} from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'BaseChat';
  percentage:any;
  url:any;
  constructor(public router: Router,private chat: ChatService, private db: AngularFireDatabase,private afAuth: AngularFireAuth,private storage:AngularFireStorage) {

  }

  async featurePhotoSelected(event: any){
    const file: File = event.target.files[0];
    console.log("Selected file : " ,file.name)
  
    const metaData = {'contentType': file.type};
    const randomId = Math.random().toString(36).substring(2);
    const ref = this.storage.ref(randomId);
    const task = this.storage.upload(randomId,file);
    this.percentage=task.percentageChanges()
    task.snapshotChanges().pipe(finalize(()=>{
      this.url = ref.getDownloadURL()
      console.log(this.url)
      this.url.subscribe((url)=>{this.chat.sendMessage(url)})
      //this.chat.sendMessage(this.chat.getTimeStamp());
    })).subscribe();
  }
}
