  getTimeStamp(): string {
    //throw new Error("Method not implemented.");
    const now = new Date();
    const date = now.getUTCFullYear() + '/' + (now.getUTCMonth() + 1 )+ '/' + now.getUTCDay();
    const time = now.getUTCHours() + ':' + (now.getUTCMinutes() + 1 )+ ':' + now.getUTCSeconds();
    return date + " " + time;
  }
  
  getMessages(): AngularFireList<any> {
    // query to create our message feed binding
    return this.db.list('/Chat', ref => ref.orderByKey().limitToLast(15));
    
  }

