import { Pipe, PipeTransform } from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import { ChatRoomComponent } from './chat-room/chat-room.component';

@Pipe({ name: 'filesize' })
export class FileSizePipe implements PipeTransform {
  transform(value: string): string {
    
    return value.replace(':)', '😊').replace(':D','😃').replace(':diable:','😈').replace(':(','😕').replace(':hm:','🤔').replace(':lol:','😆').replace(';)','😉');
  }
}

@Pipe({ name: 'youtubeImage' })
export class Image implements PipeTransform {
  constructor(private sanitizer: DomSanitizer) {
  }
  transform(value: string): any {
    console.log(value);
    if (value.search('https://firebasestorage.googleapis.com/v0/b/chat-room-c61ac')==0){
      const valueHtml = `<a data-fancybox="gallery"  href="${value}"><img style="height:100px;width:120px;" classe="imageChat" src="${value}"  /></a>`;
      return this.sanitizer.bypassSecurityTrustHtml(valueHtml);
    } else if (value.search('https://www.youtube.com/watch')==0){
      
      const value2=this.sanitizer.bypassSecurityTrustUrl(value);
      var url = "https://www.youtube.com/embed/";
      var videoId = this.getId(value);
      url = url + videoId;
      this.sanitizer.bypassSecurityTrustUrl(url);
      const value3 = `<iframe class="youtubeChat" frameborder="0" type="text/html" 
      src="${url}">
      </iframe>`;
      const value4 = `<iframe class="youtubeChat" frameborder="0" type="text/html" 
      src="${url}">
      </iframe><a data-fancybox="gallery"  href="${value}"> <img style="height:50px;width:70px;margin-bottom:100px;" src="../assets/img/youtube.png" /></a>`;
      
      return this.sanitizer.bypassSecurityTrustHtml(value4);
    }
    else {
      return value;
    }
  }



  getId(url) {
    var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
    var match = url.match(regExp);

    if (match && match[2].length == 11) {
        return match[2];
    } else {
        return 'error';
    }
}
}

@Pipe({ name: 'youtube' })
export class Youtube implements PipeTransform {
  constructor(private sanitizer: DomSanitizer) {
  }
  transform(value: string): any {
    console.log(value);
    if (value.search('https://www.youtube.com/watch')==0){
      
      const value2=this.sanitizer.bypassSecurityTrustUrl(value);
      var url = "https://www.youtube.com/embed/";
      var videoId = this.getId(value);
      url = url + videoId;
      this.sanitizer.bypassSecurityTrustUrl(url);
      const value3 = `<iframe class="youtubeChat" frameborder="0" type="text/html" 
      src="${url}">
      </iframe>`;
      return this.sanitizer.bypassSecurityTrustHtml(value3);
    }else {
      return value;
    }
  }

  getId(url) {
    var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
    var match = url.match(regExp);

    if (match && match[2].length == 11) {
        return match[2];
    } else {
        return 'error';
    }
}
}

@Pipe({ name: 'saut' })
export class Saut implements PipeTransform {
  transform(value: string): string {
    return value.replace(/\n/g, "<"+"br/>");
  }
}
